html {
  font-size: 62, 5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  background-color: #F2E5D7;
  margin: 0;
}

h1 {
  font-size: 2.5rem;
  font-family: "Anton", sans-serif;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

p {
  font-family: "Oswald", sans-serif;
}

.container-nav {
  height: 100vh;
}
.container-nav .container-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}
.container-nav .container-header h1 {
  margin: 1rem 0;
  margin-left: 2rem;
}

.button-siging {
  display: flex;
  justify-content: right;
}
.button-siging button {
  display: block;
  font-size: 1.8rem;
  width: 10rem;
  font-family: "Oswald", sans-serif;
  font-weight: bold;
  background-color: transparent;
  margin-right: 2rem;
  border: 2px solid #262322;
  cursor: pointer;
  position: relative;
}
.button-siging button:hover:after {
  content: "";
  width: 10rem;
  height: 2.8rem;
  background-color: blue;
  position: absolute;
  right: 10rem;
  top: 0;
}